@use 'abstracts' as *;

.dashboard {
  @include flexing(
    $basis: 100%);
  background-color: white;
  & .grey-space {
    flex-grow: 1;
    background-color: grey;
  }
}

nav {
  @include flexing(
    $wrap: wrap,
    $dire: row,
    $justCont: center);
  background-color: rgba(9, 0, 80, 0.678);
}