@use '../abstracts' as *;

.traffic {
  @include flexing(
    $dire: row,
    $wrap: wrap);
  padding: 0px 19px 15px 19px;
  border-bottom: 1px solid grey;
  & h2 {
    flex-basis: 100%;
  }
  & p {
    @include flexing(
      $basis: 100%,
      $justCont: center,
      $alignItem: center);
    height: 150px;
  }
}

