.flexing-socials {
  @include flexing(
    $basis: 100%);
  margin: 10px 0;
  border: 1px solid #c5c5c5;
  border-radius: 10px;
  background-color: rgba(173, 169, 169, 0.189);
  height: 90px;
  color: $primary-color;
  fill: $primary-color;
  &-title {
    margin: .5em auto 0px .3em;
  }
  &-numbers {
    margin: 0px;
    color: #393939aa;
  }
}
