@use 'abstracts' as *;

.message-label {
    margin-bottom: 14px;
}

.message-us {
  @include flexing(
    $dire: column,
    $wrap: wrap);
  padding: 10px 19px 25px 19px;
  border-bottom: 1px solid grey;
}
  
.message-search {
  margin: 5px 0px;
  padding: 1px 10px;
  height: 30px;
  background-color: #c1bbbb26;
  border-radius: 5px;
  border: 1px solid rgba(152, 152, 152, 0.693);
  outline: none;
}

.textarea {
  margin: 5px 0px;
  padding: 10px;
  height: auto;
  background-color: #c1bbbb26;
  border-radius: 5px;
  border: 1px solid rgba(152, 152, 152, 0.693);
  outline: none;
  overflow: auto;
  resize: vertical;
  font-family: "Open Sans", sans-serif;
  &::placeholder {
    color:rgba(102, 102, 102, 0.437)
  }
}
  

  
input::placeholder {
  color:rgba(102, 102, 102, 0.437)
}

.settings {
  @include flexing(
    $wrap: wrap,
    $dire: row,
    $justCont: space-between,
    $alignItem: center,
    $alignCont: space-between) {
      row-gap: 1em;
    }
  padding: 10px 19px 0;
  & h2 {
    flex-basis: 100%;
  }
}

.time-zone {
  flex-basis: 100%;
  outline: none;
  height: 2.5em;
  margin-top: 20px;
  background-color: #c1bbbb26;
  border-radius: 5px;
  border: 1px solid rgba(152, 152, 152, 0.693);
  color: rgb(133, 133, 133);
}
select:required:invalid {
  color: rgba(184, 184, 185, 0.72);
}
option[value=""][disabled] {
  display: none;
}
option {
  color: rgba(184, 184, 185, 0.72);
}