@use 'abstracts' as *;

.new-members {
    @include flexing(
        $dire: row,
        $wrap: wrap,
        $justCont: center);
    padding: 0px 19px 15px 19px;
    border-bottom: 1px solid grey;
    & h2 {
      flex-basis: 100%;
    }
}
  
.flexing-members {
    @include flexing(
        $dire: column,
        $alignItem: center) {
            flex-grow: 1;
        }
    border-top: 1px solid rgba(174, 172, 172, 0.427);
    padding: 25px 0 20px;
    & a {
        color: black;
    }
    & p {
        margin: 5px auto auto;
    }
}

.recent-activity {
    @include flexing(
        $dire: row,
        $wrap: wrap);
    padding: 10px 19px 25px 19px;
    border-bottom: 1px solid grey;
    & h2 {
      flex-basis: 100%;
    }
}

